@import 'https://fonts.googleapis.com/css?family=Montserrat:400,700';
@font-face {
  font-family: 'Gumption';
  font-style: normal;
  font-weight: normal;
  src: url("fonts/gumption-lite-webfont.woff2") format("woff2"), url("fonts/gumption-lite-webfont.woff") format("woff");
}

.wrapper {
  margin: 0 auto;
  width: 1200px;
}

.wrapper::after,
.row::after {
  clear: both;
  content: ' ';
  display: table;
}

[class*='col-'] {
  float: left;
  min-height: 1px;
  padding: 1rem;
  width: 100%;
}

[class*='col-'].no-padding {
  padding: 0;
}

@media only screen and (min-width: 20em) {
  .wrapper {
    max-width: 1200px;
    width: 100%;
  }
  .col-1 {
    width: 8.3333333333%;
  }
  .col-2 {
    width: 16.6666666667%;
  }
  .col-3 {
    width: 25%;
  }
  .col-4 {
    width: 33.3333333333%;
  }
  .col-5 {
    width: 41.6666666667%;
  }
  .col-6 {
    width: 50%;
  }
  .col-7 {
    width: 58.3333333333%;
  }
  .col-8 {
    width: 66.6666666667%;
  }
  .col-9 {
    width: 75%;
  }
  .col-10 {
    width: 83.3333333333%;
  }
  .col-11 {
    width: 91.6666666667%;
  }
  .col-12 {
    width: 100%;
  }
}

*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  outline: 0;
  padding: 0;
}

html {
  font-size: 14px;
}

